import theme from "../theme";

export default ({ name, selected, ...rest }) => (
  <div
    className={`root ${selected ? "selected" : ""}`}
    style={{
      padding: `${theme.spacing.s} ${theme.spacing.m}`,
    }}
    {...rest}
  >
    {name}
    <style jsx>
      {`
        .root:hover {
          background: gray;
        }

        .root.selected {
          background: black;
          color: white;
        }
      `}
    </style>
  </div>
);
